import React, { useState } from 'react';
import JSONData from '../assets/data/cshirts.json';
import CShirtsPreview from '../components/cshirts/cshirtPreview';
import Select from 'react-select';
import Layout from '../components/layout';
import InnerPageTitle from '../components/utils/inner-page-title';
import SelectOption from '../model/selectoption';
import PreMintMessage from '../components/utils/premintmessage';
import Constants from '../utils/constants';
import {Link} from 'gatsby';

const CShirtsPage = () => { 
    const [page, setPage] = useState(1);
    const [team, setTeam] = useState([]);
    const [role, setRole] = useState([]);
    const [color, setColor] = useState([]);
    const [number, setNumber] = useState([]);
    const [captain, setCapitain] = useState();
    const teams:SelectOption[] = [];
    const numbers:SelectOption[] = [];
    const roles:SelectOption[] = [];
    const colors:SelectOption[] = [];
    const captains:SelectOption[] = [{ value: 'Yes', label: 'Yes' }];

    JSONData.cshirts.map((shirt:any) => {
        if (!teams.find((obj:any) => obj.value === shirt.Team)) {
            teams.push({ value: shirt.Team, label: shirt.Team });
        }
        if (!roles.find((obj:any) => obj.value === shirt.Role)) {
            roles.push({ value: shirt.Role, label: shirt.Role });
        }
        if(shirt.PrimaryColor && !findValue(colors, shirt.PrimaryColor)) {
            colors.push({ value: shirt.PrimaryColor, label: shirt.PrimaryColor });
        }
        if(shirt.SecondaryColor && !findValue(colors, shirt.SecondaryColor)) {
            colors.push({ value: shirt.SecondaryColor, label: shirt.SecondaryColor });
        }
        if(shirt.TertiaryColor && !findValue(colors, shirt.TertiaryColor)) {
            colors.push({ value: shirt.TertiaryColor, label: shirt.TertiaryColor });
        }
    });
    for (let i = 0; i <= 99; i++) {
        numbers.push({ value: i.toString(), label: i.toString()});
    }
    teams.sort((team1, team2) => team1.value > team2.value ? 1 : -1);
    roles.sort((role1, role2) => role1.value > role2.value ? 1 : -1);
    colors.sort((color1, color2) => color1.value > color2.value ? 1 : -1);
    
    const filterShirts = (shirt: any, id: number) => {   
        if((team.length > 0 && findValue(team, shirt.Team) == false)
            || (number.length > 0 && findValue(number, shirt.Number) == false)
            || (captain && captain.value && captain.value != shirt.Captain)
            || (color.length > 0 && (findValue(color, shirt.PrimaryColor) == false && findValue(color, shirt.SecondaryColor) == false && findValue(color, shirt.TertiaryColor) == false))
            || (role.length > 0 && findValue(role, shirt.Role) == false)) {
            return false;
        }
        return true;        
    }; 
    
    function findValue(list:SelectOption[], value:string) {
        var obj = list.find(obj => obj.value == value);
        return obj !== undefined && obj !== null;
    };

    function isFilerNull() {
        return (team.length == 0 && number.length == 0 && role.length == 0 && color.length == 0 && !captain);
    };

    function getData() {
        if(isFilerNull()) {
            return JSONData.cshirts.slice(0, page * 98);
        }
        return JSONData.cshirts.filter(filterShirts);
    };

    const onTeamChange = (selectedOption:any) => { 
        setTeam(selectedOption);
    };
    const onNumberChange = (selectedOption:any) => { 
        setNumber(selectedOption);
    };
    const onRoleChange = (selectedOption:any) => { 
        setRole(selectedOption);
    };
    const onColorChange = (selectedOption:any) => { 
        setColor(selectedOption);
    };
    const onCaptainChange = (selectedOption:any) => { 
        setCapitain(selectedOption);
    }; 

    const loadMore = () => {
        setPage(page + 1);
    };

    const PagePreMint = <div className="row">
        <div className="col-md-12 center">
            <PreMintMessage />
        </div>
    </div>;
    const PageAfterMint = <div className="row">
        <div className="col-md-3 z-index">
            <div className="seclect-box">
                <Select placeholder="Select teams ..." options={teams} isSearchable="true" isMulti isClearable="true" name="team" className="dropdown selector-drop" onChange={onTeamChange} value={team} />
                <Select placeholder="Select numbers ..." options={numbers} isSearchable="true" isMulti isClearable="true" name="number" className="dropdown selector-drop" onChange={onNumberChange} value={number} />
                <Select placeholder="Select roles ..." options={roles} isSearchable="true" isMulti isClearable="true" name="role" className="dropdown selector-drop" onChange={onRoleChange} value={role} />
                <Select placeholder="Select captain ..." options={captains} isSearchable="true" isClearable="true" name="captain" className="dropdown selector-drop" onChange={onCaptainChange} value={captain} />
                <Select placeholder="Select colors ..." options={colors} isSearchable="true" isMulti isClearable="true" name="color" className="dropdown selector-drop" onChange={onColorChange} value={color} />
            </div>
        </div>
        <div className="col-md-9">
            <div className="wrap-fx">
                {getData().map((shirt:any) => (
                    <div className="fl-item" key={shirt.ID}>
                        <CShirtsPreview id={shirt.ID} url="/cshirts/" imgpath="/images/cshirts/72x72/" collection="C-Shirts" />
                    </div>
                ))} 
            </div>
            <div className={isFilerNull() ? 'btn-about center m-t16' : 'hide'}>
                <a className="tf-button btn-effect loadmore st2" onClick={loadMore}>   
                    <span className="boder-fade"></span>                                     
                    <span className="effect">Load More</span>
                </a>
            </div>
        </div>                    
    </div>;
    
    const PageContent = Constants.PREMINT ? PagePreMint : PageAfterMint;
    const pageSubTitle = "A generative collection of " + Constants.CSHIRTS_PUBLICSALE_SUPPLY + " jerseys 24x24px that get you multiple perks over the game";
    
    return (<Layout pageTitle="Non Fungible Cup: C-Shirts" container="inner-page">
        <InnerPageTitle title="C-SHIRTS" subtitle={pageSubTitle} />
        <section className="tf-section tf-item">
            <div className="container">
                <div className="row">
                    <div className="col-md-6">
                        <div className="info-box">
                            C-Shirts utilities.
                            <ul>
                                <li>Lifetime access to the free-minting private sale of each Tournament drop</li>
                                <li>Receive airdrop of new Tournament C-Shirts</li>
                                <li>The right to vote about next year's tournament planning</li>
                            </ul>
                        </div> 
                    </div>
                    <div className="col-md-6">
                        <div className="info-box">
                            How to get a C-Shirt?
                            <ul>
                                <li><Link to="/premint">Reserve a spot</Link> in the WL to access the free-minting <Link to="/mint">private sale</Link> of {Constants.CSHIRTS_PRIVATESALE_SUPPLY} C-Shirts (1 per wallet).</li>
                                <li>Mint the other {Constants.CSHIRTS_PUBLICSALE_SUPPLY - Constants.CSHIRTS_PRIVATESALE_SUPPLY} C-Shirts at the <Link to="/mint">public sale</Link> (no limit for each wallet)</li>
                                <li>Check the secondary market on <a href={Constants.CSHIRTS_OPENSEALINK}>Opensea</a></li>
                            </ul>
                        </div> 
                    </div>
                </div>
                {PageContent}
            </div>
        </section>
    </Layout>
    )
}
  
export default CShirtsPage;